
import {
  IonButtons,
  IonCard,
  IonCardContent,
  IonCardHeader,
  IonCardSubtitle,
  IonCardTitle,
  IonContent,
  IonHeader,
  IonIcon,
  IonPage,
  IonTitle,
  IonToggle,
  IonToolbar,
} from '@ionic/vue';

import { book, moon, sunnyOutline } from 'ionicons/icons';

import { defineComponent, ref } from 'vue';

import names from 'human-names';

import allAvatars from '../components/avatars';

export default defineComponent({
  components: {
    IonButtons,
    IonCard,
    IonCardContent,
    IonCardHeader,
    IonCardSubtitle,
    IonCardTitle,
    IonContent,
    IonHeader,
    IonIcon,
    IonPage,
    IonTitle,
    IonToggle,
    IonToolbar,
  },
  setup() {
    let themeIcon = ref(sunnyOutline);
    let isDarkMode = false;

    const positions = [
      'Business Capability Expert',
      'People Expert',
      'Process Expert',
      'Technology Expert',
    ];

    const avatars = allAvatars.map((avatar) => {
      const component = avatar.component;
      const givenName = names.allRandom();
      const surName = names.allRandom();
      const name = `${givenName} ${surName}`;

      const position = positions[Math.floor(Math.random() * positions.length)];

      return {
        name,
        position,
        component,
      };
    });

    return {
      avatars,
      icons: {
        book,
        theme: themeIcon,
      },
      toggleTheme: () => {
        isDarkMode = !isDarkMode;
        themeIcon.value = isDarkMode ? moon : sunnyOutline;
        document.body.classList.toggle('dark', isDarkMode);
      },
    };
  },
});
